
.flow-realname {
  background: #f5f5f5;
}
.flow-realname header {
    /*position: fixed;*/
    /*top: 0;*/
    position: relative;
    width: 100%;
    height: .9rem;
    font-size: .32rem;
    line-height: .9rem;
    text-align: center;
    color: #666;
    background-color: #fff;
    box-shadow: 0 .02rem .02rem 0 #e9ebed;
}
.flow-realname .arrow-back {
    position: absolute;
    margin: auto;
    left: .28rem;
    top: 0;
    bottom: 0;
    width: .3rem;
    height: .3rem;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    border-left: 1px solid #cecece;
    border-bottom: 1px solid #cecece;
}
.flow-realname .r-href {
    position: absolute;
    right: .2rem;
    font-size: .26rem;
    color: #5781bd;
    text-decoration: none;
}
.flow-realname .verify .f-default {
    padding: .9rem 0 .16rem;
    font-size: 0;
    text-align: center;
}
.flow-realname .verify h3 {
    font-size: .32rem;
    line-height: 1.5;
    text-align: center;
    color: #f8b551;
}
.flow-realname .verify .subtit {
    padding-top: .08rem;
    font-size: .26rem;
    line-height: 1.5;
    text-align: center;
    color: #999;
}
.flow-realname .verify .top-margin {
    margin-top: .94rem;
}
.flow-realname .form-tpd {
    padding-top: .82rem;
}
.flow-realname .form {
    margin-top: .2rem;
    background-color: #fff;
}
.flow-realname .form li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-left: .2rem;
    height: .9rem;
    border-bottom: 1px solid #eee;
    overflow: hidden;
}
.flow-realname .form li:last-of-type {
    border-bottom: none;
}
.flow-realname .form .key {
    font-size: .26rem;
    line-height: .84rem;
    color: #666;
}
.flow-realname .form .value {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    margin-right: .2rem;
    font-size: .26rem;
    line-height: .84rem;
    text-align: right;
    color: #f8b551;
}
.flow-realname .form label {
    -ms-flex-item-align: center;
        -ms-grid-row-align: center;
        align-self: center;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    margin-right: .38rem;
    padding-right: .4rem;
    height: .3rem;
    border-right: 1px solid #eee;
    font-size: .26rem;
    line-height: .3rem;
    color: #666;
}
.flow-realname .form input {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    -ms-flex-negative: 1;
        flex-shrink: 1;
    margin-right: .2rem;
    min-width: 2.2rem;
    border: none;
    outline: none;
    font-size: .26rem;
}
.flow-realname .form input::-webkit-input-placeholder {
    color: #ccc;
}
.flow-realname .form .f-btn {
    width: 2rem;
    font-size: .26rem;
    line-height: .9rem;
    text-align: center;
    color: #fff;
    background-color: #5781bd;
}
.flow-realname .form .bank {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    padding: 0 .1rem;
    margin-left: -.1rem;
    margin-right: .2rem;
    border: none;
    outline: none;
    font-size: .26rem;
    line-height: .9rem;
    text-overflow: ellipsis;
    color: #666;
    -webkit-appearance: none;
    -moz-appearance: none;
}
.flow-realname .agreement {
    position: relative;
    padding-top: .16rem;
    padding-left: .54rem;
    font-size: 0;
}
.flow-realname .agreement p {
    font-size: .26rem;
    line-height: 1.5;
    color: #666;
}
.flow-realname .agreement p a {
    color: #ccc;
    text-decoration: none;
}
.flow-realname .agreement a {
    font-size: .26rem;
    line-height: 1.5;
    color: #ccc;
    text-decoration: none;
}
.flow-realname .checkbox {
    position: absolute;
    left: .18rem;
    top: .22rem;
    width: .3rem;
    height: .3rem;
}
.flow-realname .checkbox input[type=checkbox] {
    display: none;
}
.flow-realname .checkbox input[type=checkbox] ~ label {
    display: inline-block;
    width: .3rem;
    height: .3rem;
    font-size: .26rem;
    line-height: 1.5;
    background: url(../../assets/check.png) no-repeat center/contain;
}
.flow-realname .checkbox input[type=checkbox]:checked ~ label {
    background: url(../../assets/checked.png) no-repeat center/contain;
}
.flow-realname .btn {
    margin-top: .4rem;
    margin-left: .2rem;
    margin-right: .2rem;
    height: .9rem;
    font-size: .26rem;
    line-height: .9rem;
    text-align: center;
    color: #fff;
    overflow: hidden;
    border-radius: .03rem;
    background-color: #f14d4c;
}
.flow-realname .h50 {
    height: .5rem;
}



